/* Map */
div#root {
  background-color: #fff;
}

.leaflet-container {
  height: 600px;
  width: 1000px;
  margin: 0 auto;
  font-size: 90%;
  line-height: 18px;
  font-family: Baskerville, 'Goudy Old Style', serif;
}

#feature-popup {
  min-height: 300px;
}

.react-tabs__tab-panel > table {
  width: 100%;
  align: center;
}

.leaflet-popup-content-wrapper .leaflet-popup-content tr:nth-child(even) {
  background-color: #dee8f2;
}
.leaflet-popup-content-wrapper .leaflet-popup-content table,
td,
tr {
  border: 1px solid black;
  border-collapse: collapse;
  padding: 8px;
}

.popup-container .popup-content {
  white-space: normal;
  min-width: 175px;
  word-wrap: break-word;
}
.map-legend {
  position: absolute;
  bottom: 15px;
  z-index: 500;
  border: 1px solid black;
  background: #ddd;
  opacity: 0.85;
  width: 250px;
}
.map-legend .legend-title {
  text-align: center;
  position: relative;
  font-weight: bold;
}
.map-legend .legend-scale ul {
  margin-right: 0;
  margin-left: 0;
  margin-top: 5px;
  margin-bottom: 5px;
  padding: 0;
  float: left;
  list-style: none;
  position: relative;
}
.map-legend .legend-scale ul li {
  font-size: 90%;
  list-style: none;
  margin-left: 5px;
  margin-right: 5px;
  line-height: 18px;
  margin-bottom: 2px;
}
.map-legend ul.legend-labels li span.color-block {
  display: block;
  float: left;
  width: 10%;
  height: 16px;
  margin-right: 5px;
  margin-left: 0;
  border: 1px solid #999;
}
.map-legend ul.legend-labels li span.label-text {
  white-space: normal;
  word-wrap: break-word;
  display: inline-block;
  width: 85%;
  margin-right: 5px;
  margin-left: 0;
}
